export class BaseField<T> {
  type: string
  label: string;
  key: string;
  class: string;

  constructor(options: {
    label?: string,
    key?: string,
    class?: string,
  } = {}) {
    this.label = options.label || null;
    this.key = options.key || null;
    this.class = options.class || null;
  }
}

export class IndexField extends BaseField<string> {
  type = 'index';

  constructor(options: {} = {}) {
    super(options);
  }
}

export class TextField extends BaseField<string> {
  type = 'text';

  constructor(options: {} = {}) {
    super(options);
  }
}

export class PriceField extends BaseField<string> {
  type = 'price';

  constructor(options: {} = {}) {
    super(options);
  }
}

export class DateField extends BaseField<string> {
  type = 'date';

  constructor(options: {} = {}) {
    super(options);
  }
}

export class StatusField extends BaseField<string> {
  type = 'status';

  constructor(options: {} = {}) {
    super(options);
  }
}

export class MultiStatusField extends BaseField<string> {
  type = 'multistatus';

  constructor(options: {} = {}) {
    super(options);
  }
}

export class TextClickEventField extends BaseField<string> {
  type = 'textClickEvent';

  constructor(options: {} = {}) {
    super(options);
  }
}


export class TextViewUrlField extends BaseField<string> {
  type = 'textUrl';
  viewUrl: {
    path: string,
    id: string
  }

  constructor(options: {} = {}) {
    super(options);
    this.viewUrl = options['viewUrl'] || null;

  }
}


export class ActionField extends BaseField<string> {
  type = 'action';

  view: {
    path: string,
    id: string,
    showText: string,
    class: string
  };


  edit: {
    path: string,
    id: string,
    showText: string,
    class: string
  };

  delete: {
    path: string,
    id: string
  };


  constructor(options: {} = {}) {
    super(options);
    this.label = options['label'] || 'Action';
    this.key = options['key'] || 'action';
    this.class = options['class'] || 'text-right';
    this.view = options['view'] || null;
    this.edit = options['edit'] || null;
    this.delete = options['delete'] || null;
  }

}

export class CheckBoxFildes extends BaseField<string> {
  type = 'checkbox';

  constructor(options: {} = {}) {
    super(options);
    this.label = options['label'] ||
      `checkbox`;
    this.class = options['class'] || 'text-right';

  }
}
