import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'objectValue'
})
export class ObjectValuePipe implements PipeTransform {

  transform(obj: any, args?: any): any {
    let value = obj;

    if (args) {
      let attrList = args.split('.');
      attrList.forEach(key => {
        if (key && value) {
          value = value[key]
        }
      });
      value = value ? value : '-';
    } else {
      value = '-';
    }
    return value;
  }
}


@Pipe({
  name: 'getViewUrl'
})
export class getViewUrlPipe implements PipeTransform {

  transform(obj: any, args?: any): any {


    let urlpath = args.path;
    let id = obj;
    let attrList = args.id.split('.')
    attrList.forEach(key => {
      id = id[key]
    });
    urlpath += String(id)
    return urlpath;
  }

}


